body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

div.bareme-sub-item {
   display: flex;
   margin-bottom: 10px;
}

/* center span content text vertically */
div.bareme-sub-item span {
   display: inline-flex;
   align-items: center;
}

div.bareme-sub-item>span.title {
   width: 125px;
   margin-left: 20px;
}

div.bareme-sub-item>button {
   width: 35px;
}

div.bareme-sub-item>div.value {
   width: 30px;
   text-align: center;
   padding-top: 2%;
}
div.bareme-settings {
   margin-bottom: 25px;
}

div.bareme-settings>div {
   display: flex;
}

div.bareme-settings>div>div {
   width: 33%;
   max-width: 350px;
   min-width: 350px;
}
div.custom-tooltip {
   background-color: white;
   border: 1px solid rgb(204, 204, 204);
}

div.custom-tooltip>div {
   margin-top: 10px;
   margin-bottom: 10px;
}

td.tooltip-rank-evolution-cell {
   text-align: center;
}

td.tooltip-points-cell {
   text-align: right;
}

tr.tooltip-row>td {
   padding-left: 5px;
}

.tooltip-row>td:last-child {
   padding-right: 5px;
}
div.simple-line-chart {
   width: 900px;
   height: 600px;
}
table.char-settings {
   width: 1000px;
}

div.buttons-phases-regions {
   display: flex;
   justify-content: center;
}

div.buttons-phases,
div.buttons-regions {
   justify-content: center;
}
div.ranking-barchart {
   margin-left: 200px;
}
div.results-by-team-table {
   margin-left: 20px;
}

div.results-by-team-table td+td {
   width: 115px;
}
.underline {
  text-decoration: underline;
}

* {
  font-size: 16px;
}

body {
  margin-left: 10px;
  margin-top: 50px;
  display: flex;
}

div#root {
  margin: auto;
}

div.App {
  width: 1000px;
}

